}

.cds--css-grid--condensed {
  --cds-grid-gutter: 0.0625rem;
  --cds-grid-column-hang: 0.96875rem;
}

.cds--subgrid {
  display: grid;
  margin-right: calc(var(--cds-grid-mode-end) * -1);
